.App {
  text-align: center;
  background-color: #2a2a2b;
  margin: 2%;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #c9cdd3;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header_div{
  /* height: 100px; */
  width: 100%;
  background-color: pink;
  color: 2a2a2b;
}

.header_title{
  /* float: left; */
  margin: 0%;
  padding-left: 2%;
  text-align: left;
}

.Task-details{
  display: grid;
  grid-template-columns: 1fr 20px;
  padding-left: 10px;
  padding-right: 20px;
  /* padding-top: 1%; */
  text-align: left;

}

.form-group{
  text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 10%;
    margin-right: 10%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
